*{
  margin:0;
  padding:0;
}
#carouselWrap{
  margin:100px auto;
  width:620px;
  height:630px;
  padding:5px;
  /* background:url("./images/background.gif"); */
  position:relative;
}
#carouselPrev{
  position:absolute;
  top:50%;
  left:-2%;
  cursor:pointer;
}
#carouselNext{
  position:absolute;
  top:50%;
  right:-2%;
  cursor:pointer;
}
#carouse{
  width:100%;
  height:100%;
  overflow:hidden;
}
#carouselInner ul.column{
  width:605px;
  height:600px;
  padding:15px 0 15px 15px;
  list-style-type:none;
  float:left;
}
#carouselInner ul.column li{
  float:left;
  margin-right:10px;
  display:inline;
}
#carouselInner ul.column li img{
  border:none;
}
